import React, { useEffect, useState, useRef } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../../components/table";
// import useDivision from "../../store/division";
import Button from "../../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { COLORS } from "../../../helpers/constants";
import AllMatScan from "./AllMatScan"
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { t } from "i18next";
import useJob from "../../../store/operatorPortal/job";

const AssemblyScan = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const locationData = location.state
  const { OrderMaterialList, createAssembly, currentAssemblyOrderStatus,updateAssemblyOrderStatus, getMachineStatus,AssemblyMaterialList } = useJob()
  const [tableData, setTableData] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });

  const [payload, setPayload] = useState()
  const [machineId, setMachineId] = useState(localStorage.getItem('machineId') || "");

  const [runningStatus, setRunningStatus] = useState(false)
  const [isConfirmDisabled, setIsConfirmDisabled] = useState(true);

  // useEffect(() => {
  //   if (!tableData.length) {
  //     setIsConfirmDisabled(true);
  //     return;
  //   }
  
  //   const allMaterialsScanned = tableData.every((material) => {
  //     return payload?.[material.material_name]?.length > 0; // Check if material has been scanned at least once
  //   });
  
  //   setIsConfirmDisabled(allMaterialsScanned);
  //   console.log("table data-------",tableData)
  // }, [payload, tableData]);
  useEffect(() => {
    if (!tableData.length) {
      setIsConfirmDisabled(true);
      return;
    }
  
    // Check if all issued items in every material have scanned: true
    const allMaterialsScanned = tableData.every((material) =>
      material.issued.every((issuedItem) => issuedItem.scanned === true)
    );
  
    setIsConfirmDisabled(allMaterialsScanned); // Disable button if not all are scanned
  }, [tableData]); // Remove payload dependency, as we only check tableData
  


  useEffect(() => {
    console.log("workOrderTask--scan---", locationData)
    fetchMaterials();
  }, [page]);

  const fetchMaterials = async () => {
    setLoading("fetch");
    const [_list, _count] = await AssemblyMaterialList(locationData.id, page + 1);
    // const mergedData = [
    //     ...(_list?.materials || []).map(item => ({ ...item, type: "Material" })),
    //     ...(_list?.lpc || []).map(item => ({ ...item, type: "LPC Circuit" })),
    //     ...(_list?.acc || []).map(item => ({ ...item, type: "ACC Circuit" }))
    //   ];

    console.log("workOrderTask----_list-", _list)
  
      setTableData(_list.items);
    // setTableData([..._list?.materials,..._list.lpc,..._list.acc]);
    // setTotal(_count);
    setLoading(false);
    if(_list?.materials_verified){
      navigate("/assembly-machine-data",{state:locationData.id})

    }
  };

  const SubmitScanned = async () => {
    // alert("click")
    // console.log("SubmitScanned updateOrderStatus respose----", payload)
    // return
    setModalOpen('detail')
    // setLoading("fetch");
    let newReq ={
        "materials":payload
    }
    let res = await createAssembly(locationData.id,newReq );
    console.log("SubmitScanned updateOrderStatus respose----", res)
    if (res && res.message === 'OK') {
      let req = { "status": "RUNNING" }
      let res = await updateAssemblyOrderStatus(locationData.id, req)
      setRunningStatus(true)
      setTimeout(() => {
        navigate("/assembly-machine-data",{state:locationData.id})
      }, 2000);
    }else{
      setTimeout(() => {
      window.location.reload()
    }, 2000);
    }
  };


  // const handleAdd = async () => {
  //   const payload = { ...data }
  //   setLoading("fetch");
  //   let res = await createLPC(payload, locationData.id);
  //   console.log("handle add respose----", res)
  //   setModalOpen(false);
  //   // await fetchMaterials();
  //   setLoading(false);
  //   // setEdit(false)
  // };

  const handleDelete = async (id) => {
    setLoading("fetch");
    // await remove(id);
    await fetchMaterials();
    setLoading(false);
  };
  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    // await update(id, payload);
    await fetchMaterials();
    setLoading(false);
  };

  const LABELS = [
    {
      id: 1,
      title: "Name",
      render: (rowData) => rowData?.material_name,
    },
   
    {
      id: 3,
      title: "Description",
      render: (rowData) => rowData?.material_description || '-'
    },
    {
      id: 2,
      title: "Issued",
      render: (rowData) => rowData?.issued?.map((item)=><Typography sx={{backgroundColor:item.scanned?'green':COLORS.RADIAL_RED,mb:0.2}}>GRN NO : {item.grn_number} , QTY : {item.quantity}</Typography>),
    },
    // {
    //   id: 6,
    //   title: "Status",
    //   render: (rowData) => {
    //     if (!payload || !payload[rowData.material_name]) {
    //       return "-"; // Default status if payload is not yet available
    //     }

    //     // const totalScanned = payload[rowData.material_name].reduce(
    //     //   (sum, item) => sum + (item.quantity || 0),
    //     //   0
    //     // );
    //     console.log(" payload[rowData.name]----------", payload[rowData.material_name])
    //     return payload[rowData.material_name] ? <CheckCircleOutlineIcon fontSize="large" sx={{ color: COLORS.LIGHT_GREEN }} /> : "-";
    //   }
    // }
  ];

  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      handleScan(e.target.value);
      e.target.value = ""; // Clear the input for the next scan
    }
  };
  const handleScan = (data) => {
    handleBarcodeScannerModalSubmit(data);
  };

  // const handleBarcodeScannerModalSubmit = (inputData) => {
  //   const [material_name, quantityString, unit, grnNumber, grnDate] = inputData.split("#");
  //   // console.log("scan item", material_name, quantityString, unit, grnNumber, grnDate);

  //   const quantity = parseFloat(quantityString);
  //   if (isNaN(quantity)) {
  //     console.log("Invalid quantity");
  //     return;
  //   }

  //   const currentMaterial = tableData.find(
  //     (item) => item.material_name === material_name
  //   );
  //   // console.log("Material found in table data");
  //   if (!currentMaterial) {
  //     toast.warn("Material not found in bom");
  //     return;
  //   }

  //   setPayload((prevPayload) => {
  //     const updatedPayload = { ...prevPayload };
     

  //     if (!updatedPayload[material_name]) {
  //       updatedPayload[material_name] = [];
  //     }
  //     updatedPayload[material_name].push({
  //       quantity: quantity,
  //       grn_number: grnNumber 
  //     });

  //     console.log("Updated Payload Inside setPayload:", updatedPayload);
  //     return updatedPayload;
  //   });
  //   if (inputRef.current) {
  //     inputRef.current.focus();
  //   }
  // };


  
  const handleBarcodeScannerModalSubmitt = (inputData) => {
    const [material_name, quantityString, unit, grnNumber, grnDate] = inputData.split("#");
  
    const quantity = parseFloat(quantityString);
    if (isNaN(quantity)) {
      console.log("Invalid quantity");
      return;
    }
  
    const currentMaterial = tableData.find(
      (item) => item.material_name === material_name
    );
  
    if (!currentMaterial) {
      toast.warn("Material not found in BOM");
      return;
    }
  
    setPayload((prevPayload) => {
      const updatedPayload = { ...prevPayload };
  
      // Find issued GRN entry
      const issuedEntry = currentMaterial.issued

      if (!issuedEntry) {
        toast.warn(`Material not issued for ${material_name}`);
        return prevPayload;
      }

       let scannedGRNandQuantity = {
        quantity: quantity,
        grn_number: grnNumber,
      }
        setTableData((prevItems) =>
          prevItems.map((item) =>
            item.material_name === currentMaterial
              ? {
                  ...item,
                  issued: item.issued.map((issuedItem) => ({
                    ...issuedItem,
                    scanned: true // Adding or updating scanned status
                  }))
                }
              : item
          )
        );
  
      // Add entry to payload
      if (!updatedPayload[material_name]) {
        updatedPayload[material_name] = [];
      }
      updatedPayload[material_name].push({
        quantity: quantity,
        grn_number: grnNumber,
      });
  
      console.log("Updated Payload Inside setPayload:", updatedPayload);
      return updatedPayload;
    });
  
    if (inputRef.current) {
      inputRef.current.focus();
    }
  };

  const handleBarcodeScannerModalSubmit = (inputData) => {
    const [material_name, quantityString, unit, grnNumber, grnDate] = inputData.split("#");
  
    const quantity = parseFloat(quantityString);
    if (isNaN(quantity)) {
      console.log("Invalid quantity");
      return;
    }
  
    const currentMaterial = tableData.find(
      (item) => item.material_name === material_name
    );
  
    if (!currentMaterial) {
      toast.warn("Material not found in BOM");
      return;
    }
  
    setPayload((prevPayload) => {
      const updatedPayload = { ...prevPayload };
  
      // Find issued GRN entry
      const issuedEntries = currentMaterial.issued;
  
      if (!issuedEntries || issuedEntries.length === 0) {
        toast.warn(`Material not issued for ${material_name}`);
        return prevPayload;
      }
  
      let scannedGRNandQuantity = {
        quantity: quantity,
        grn_number: grnNumber,
      };
  
      // Update tableData to set scanned: true for matching issued items
      // setTableData((prevItems) =>
      //   prevItems.map((item) =>
      //     item.material_name === material_name
      //       ? {
      //           ...item,
      //           issued: item.issued.map((issuedItem) =>
      //             issuedItem.quantity === scannedGRNandQuantity.quantity &&
      //             issuedItem.grn_number === scannedGRNandQuantity.grn_number
      //               ? { ...issuedItem, scanned: true } // Mark as scanned
      //               : issuedItem
      //           ),
      //         }
      //       : item
      //   )
      // );
      setTableData((prevItems) =>
        prevItems.map((item) => {
          if (item.material_name !== material_name) return item;
      
          let found = false; // Flag to update only one item
      
          return {
            ...item,
            issued: item.issued.map((issuedItem) => {
              if (
                !found &&
                // issuedItem.quantity === scannedGRNandQuantity.quantity &&
                issuedItem.grn_number === scannedGRNandQuantity.grn_number &&
                !issuedItem.scanned // Ensure it's not already scanned
              ) {
                found = true; // Mark this one, skip others
                return { ...issuedItem, scanned: true };
              }
              return issuedItem;
            }),
          };
        })
      );
      
  
      // Add entry to payload
      if (!updatedPayload[material_name]) {
        updatedPayload[material_name] = [];
      }
      updatedPayload[material_name].push({
        quantity: quantity,
        grn_number: grnNumber,
      });
  
      console.log("Updated Payload Inside setPayload:", updatedPayload);
      return updatedPayload;
    });
  
    if (inputRef.current) {
      inputRef.current.focus();
    }
  };
  
  
  useEffect(() => {
    console.log("Updated Payload in useEffect:", payload);
  }, [payload]);

  const inputRef = useRef(null);

  useEffect(() => {
    if (inputRef.current) {
      inputRef.current.focus();
    }
  }, []);

  return (
    <div className="mx-3 my-2">
      <div className=" items-center mb-2">
      <div style={{ display: 'flex', flexDirection: 'row', gap: 2,alignItems:'center' }}>
        <IconButton onClick={() => navigate(-1)}>
          <ArrowBackIcon />
        </IconButton>
        <h1 className="mt-4">{locationData.fg_code} &gt; {locationData.work_order_order_id}</h1>
        </div>
        <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
          <h1 className="fw-24 mt-1">Please scan the materials and circuits</h1>
          <input ref={inputRef} style={{ height: '1.5rem', width: '26rem' }} type="text" onKeyPress={handleKeyPress} />
        </Stack>
      </div>
      <Table
        // headColor={COLORS.TEXT_GRAY}
        // headLabelColor={COLORS.WHITE}
        columns={LABELS}
        rows={tableData}
        // rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        // paginator
        serialize
        total={total}
      />
      <AllMatScan
        open={modalOpen === "detail"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        edit={edit}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      // sx={{px:6}}
      >
        <Button bordered gray >
          Cancel
        </Button>
        <Button color={'light_green'} onClick={() => SubmitScanned()} disabled={!isConfirmDisabled}>
          <Typography color={COLORS.TEXT_GRAY}>
            Confirm
          </Typography>
        </Button>
      </Stack>
    </div>
  );
};

export default AssemblyScan;
