import React, { useEffect, useState, useRef } from "react";
import { Stack, Typography, Grid } from "@mui/material";
import Button from "../../../components/button";
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import { COLORS } from "../../../helpers/constants";
import crossIcon from '../../../assets/svg/cross-icon.svg'
import { t } from "i18next";
import useJob from "../../../store/operatorPortal/job";
import Downtime from "./Downtime";
// import Barcode from 'react-barcode';
import QRCode from "react-qr-code"


import RadialBarChart from './gauge'
import { replace } from "lodash";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../../components/table";
import PrintingPage from './Printing'


const AssemblyMachineData = ({ open, onClose, data }) => {
  const navigate = useNavigate()
  const location = useLocation()
  const locationData = location.state
  const [errors, setErrors] = useState(new Map());
  const [machineData, setMachineData] = useState({})
  const [machineId, setMachineId] = useState(localStorage.getItem('machineId') || "");
  const [modalOpen, setModalOpen] = useState(false);
  const [currentState, setCurrentState] = useState('')
  const [label, setLabel] = useState({})


  const { OrderMaterialList, create, updateOrderStatus, getMachineStatus, updateAssemblyOrderStatus, currentAssemblyOrderStatus, nextLabel } = useJob()


  useEffect(() => {
    console.warn("locationData----", locationData)
    MachineStatus()
    const interval = setInterval(() => {
      MachineStatus();
    }, 5000); // Runs every 10 seconds

    return () => clearInterval(interval); // Cleanup when `runningStatus` changes or component unmounts
  }, []);


  const MachineStatus = async () => {
    let res = await currentAssemblyOrderStatus(locationData)
    if (res) {
      console.log("getMachineStatus respose----", res.state)
      setCurrentState(res)
      // if (res?.event_packet?.downtime_reason) {
      //   setModalOpen(false);
      // } else if (res.asset_state === "downtime") {
      //   setModalOpen("downtime");
      // }
      // setMachineData(res)
      // if (res.total === res.completed) {

      //   navigate('/job', { replace: true })
      // }
    }
  }
  const getColor = (state) => {
    switch (state) {
      case "offline":
        return "#FF0000"; // Red
      case "online":
        return "#27AE60"; // Green
      default:
        return "#FAA036"; // Orange (default case)
    }
  };

  async function UpdateStatus(params) {
    // RUNNING 
    let req = { "status": params }
    let res = await updateAssemblyOrderStatus(locationData, req)
    await MachineStatus()
    if (params === "COMPLETED") {
      setTimeout(() => {
        navigate('/job', { state: machineId, replace: true })
      }, 1000);
    }
  }

  const handlePrint = async () => {
    let req = { "status": 'COMPLETED' }
    // let ress = await updateAssemblyOrderStatus(locationData, req)
    // setModalOpen('printing')
    const payload = {
      asset_id: machineId,
      order_id: currentState?.order?.id
    }

    let res = await nextLabel(payload)
    console.log("printing", res)
    setLabel(res)
    if (res) {
      setTimeout(() => {
        handlePrinting()

      }, 1000);
    }

  }

  const printRef = useRef(); // Ref for the printable area

  const handlePrinting = () => {
    const printContent = printRef.current.innerHTML;
    const originalContent = document.body.innerHTML;

    const style = `
      <style>
        @media print {
          @page { margin: 0; size: auto; }
          body { margin: 0; padding: 0; }
          header, footer { display: none; }
        }
      </style>
    `;

    document.body.innerHTML = style + printContent;
    window.print();
    document.body.innerHTML = originalContent;
    window.location.reload(); // Restore UI
  };

  return (
    <div className="mx-3 my-2">
      <div className=" items-center">
        <h1 className="mt-1">Order Status : {currentState?.state}</h1>
      </div>
      <Grid container spacing={2} mb={2}>
        <Grid item xs={6}>
          <Typography color="text.secondary">
            <strong>{t('FGCode')} :</strong> {currentState?.order?.fg_code}
          </Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography color="text.secondary">
            <strong>Order Id :</strong> {currentState?.order?.order_id}
          </Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography color="text.secondary">
            <strong>Order Type :</strong> {currentState?.order?.order_type}
          </Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography color="text.secondary">
            <strong>Quantity : </strong> {currentState?.order?.quantity}
          </Typography>
        </Grid>
        <Grid item xs={6}>
          <Typography color="text.secondary">
            <strong>Complete Quantity : </strong> {currentState?.completed_quantity}
          </Typography>
        </Grid>
      </Grid>

      <Stack sx={{ flexDirection: 'row', gap: 3, mb: 2, justifyContent: 'center' }}>
        {/* {currentState?.state === 'RUNNING' ? (
          <Button color={'RADIAL_RED'} onClick={() => UpdateStatus('STOPPED')}>
            <Typography color={COLORS.WHITE}>
              Pause
            </Typography>
          </Button>
        ) : (
          <Button color={'yellow'} onClick={() => UpdateStatus('RUNNING')}>
            <Typography color={COLORS.WHITE}>
              Resume
            </Typography>
          </Button>
        )}
        <Button color={'light_green'} onClick={() => UpdateStatus('COMPLETED')}>
          <Typography color={COLORS.WHITE}>
            Complete
          </Typography>
        </Button> */}
        <Button color={'green'} onClick={() => {
          handlePrint('COMPLETED');
          // handlePrinting()
        }}>
          <Typography color={COLORS.WHITE}>
            Print Label
          </Typography>
        </Button>
      </Stack>
      {/* <Stack>
        <iframe
          src="/pdf/sample.pdf"
          width="100%"
          height="600px"
          style={{ border: "none" }}>
        </iframe>
        <embed src={samplePDF} width="100%" height="600px" type="application/pdf" />

      </Stack> */}
      {/* <Stack sx={{ alignItems: 'center', justifyContent: 'center' }}>
        <img src={gearGif} alt="uploading" style={{ width: 100, height: 100, objectFit: 'contain' }} />
      </Stack> */}
      {/* <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', mt: -5 }}>
        <Stack sx={{ justifyContent: 'center', gap: 1 }}>
          <Typography className="fw-24" sx={{}}>Circuit Name : <strong>{machineData?.circuit_name}</strong></Typography>
          <Typography className="fw-24" sx={{}}>Circuit Code : <strong>{machineData?.circuit_code}</strong></Typography>
          <Typography className="fw-24" sx={{ textTransform: 'capitalize' }} >Machine Status : <strong style={{ color: getColor(machineData?.asset_state) }}>{machineData?.asset_state}</strong></Typography>
          <Typography className="fw-24" sx={{}}>Total : <strong>{machineData?.total}</strong></Typography>
          <Typography className="fw-24" sx={{}}>Completed : <strong>{machineData?.completed}</strong></Typography>
        </Stack>
        <Stack>
          <RadialBarChart total={machineData?.total} completed={machineData?.completed || 500} />
        </Stack>
      </Stack> */}
      {/* <div>
      <table style={{ width: "100%" }}>
      <tr >
        {Object.entries(machineData?.circuit_metadata).map(([key, value]) => (
         <th style={{ padding: "8px", border: "1px solid black", fontWeight: "bold", background: "#f0f0f0" ,width:'10%'}}>
           {key.charAt(0).toUpperCase() + key.slice(1)}
         </th>
        ))}
      </tr>
      <tr >
        {Object.entries(machineData?.circuit_metadata).map(([key, value]) => (
            <th style={{ padding: "8px", border: "1px solid black" ,fontWeight:'normal',width:'10%'}}>{value}</th>
        ))}
      </tr>
    </table>
    </div> */}
      {/* <div style={{}}> */}
        <div style={{ display: "none" }}>
        {label?.qr_code && (
          <div ref={printRef}>
            <Stack sx={{ width: '288px', height: '192px'}}>
              <Typography className="fw-36" sx={{ mt: 2 }} textAlign={"center"}>Dhoot Transmission</Typography>
              <Stack sx={{ justifyContent: 'space-between', gap: 1,flexDirection:'row' }}>
                <Stack sx={{ flexDirection: 'row', gap: 1 }}>
                  <Stack>
                    <Typography>Order no :</Typography>
                    <Typography>Fg code :</Typography>
                    <Typography>S.No :</Typography>
                  </Stack>
                  <Stack>
                    <Typography>{label?.order}</Typography>
                    <Typography>{label?.fg_code}</Typography>
                    <Typography>{label?.serial_number}</Typography>
                  </Stack>
                </Stack>
                
              </Stack>
              <Stack sx={{marginLeft:'70%'}}>
                  <QRCode
                    size={60}
                    // style={{ height: "auto", maxWidth: "100%", width: "100%" }}
                    value={label?.qr_code}
                    viewBox={`0 0 256 256`}
                  />
                </Stack>
            </Stack>
          </div>
        )}
      </div>
      <PrintingPage
        open={modalOpen === "printing"}
        oID={currentState}
        onClose={() => { setModalOpen(false) }}
      />
      <Downtime
        open={modalOpen === "downtime"}
        onClose={() => { setModalOpen(false) }}

        // edit={edit}
        // setData={setData}
        // onSubmit={handleAdd}
        packetData={machineData}
      />
    </div>

  );
};

export default AssemblyMachineData;
