import React, { useEffect, useState, useRef,useLayoutEffect,useMemo } from "react";
import { VariableSizeList as List } from 'react-window';
import { useReactToPrint } from 'react-to-print';
import { useLocation, useNavigate } from "react-router-dom";
// import Table from "../../components/table";
import PieChart from '../pie'
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid, TablePagination, TableFooter } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import useUser from "../../store/users";
import useMatrix from "../../store/skillmatrix";
import useSkill from "../../store/skill";
import MTable from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Input from "../../components/input";
import { SCHEMA_TYPES } from "../../helpers/constants";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import LocalPrintshopOutlinedIcon from '@mui/icons-material/LocalPrintshopOutlined';
import Tooltip from '@mui/material/Tooltip';
import PrintPage from "./index1";
import FilterAltOutlinedIcon from '@mui/icons-material/FilterAltOutlined';
import empty from '../../assets/png/empty.png'
import { useTranslation } from 'react-i18next';
import Spin from "../../components/spin";
import { t } from "i18next";
import useQuestion from "../../store/questionBank";
import Button from "../../components/button";

const SkillMatrix = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const companyId = location.state
  const [modalOpen, setModalOpen] = useState("testDetail");
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [data, setData] = useState({
    name: "",
    tranning_type: '',
    metadata: {},
    skill: '',
    aadhar:'',
    skill_id:''
  });
  const [total,setTotal] = useState(0)
  const { skillMatrixList} = useMatrix();
  const { list: userList } = useUser();
  // const { list: skill_list } = useSkill()
  const {  skill_list} = useQuestion();

  const [errors, setErrors] = useState(new Map());
  const [skills, setSkills] = useState([])
  const [matrixList, setMatrixList] = useState([])
  const [matrix_List, setMatrix_List] = useState([])
  const [printIndex, setPrintIndex] = useState({})
  const [printState, setPrintState] = useState(false)
  const [skillList, setSkillList] = useState([{
    value: 'All',
    key: '123'
  }])
  let color_1 = [COLORS.L4, COLORS.L4, COLORS.L4, COLORS.L1]
  let color_2 = [COLORS.L2, COLORS.L4, COLORS.L4, COLORS.L1]
  let color_3 = [COLORS.L2, COLORS.L3, COLORS.L4, COLORS.L1]
  let color_4 = [COLORS.L2, COLORS.L3, COLORS.L5, COLORS.L1]
  let color_5 = [COLORS.L4, COLORS.L4, COLORS.L4, COLORS.L4]

  const form2 = [
    {
    // title: "Filter By Skill",
    placeholder: t('filterBySkill'),
    type: SCHEMA_TYPES.SELECT,
    name: "skill_id",
    keyValues: skillList
  },
    {
    // title: "Filter By Skill",
    placeholder: t('search_by_aadhar'),
    type: SCHEMA_TYPES.INPUT,
    name: "aadhar",
    // keyValues: skillList
  }
]

  useLayoutEffect(() => {
    // setLoading('fetch')
    fetchSkill()
    //  fetchUsers()
  }, [])

  useEffect(()=>{
    setLoading(true)
    fetchSkillMatrix()
  },[page,data.skill_id,data.aadhar])

  async  function fetchSkillMatrix(){
    const res = await skillMatrixList(page,data.skill_id,data.aadhar)
    setTotal(res.count)
    // console.log('skillMatrixList data.skill_id',data.skill_id)
    setMatrixList(res.items);
    if(data.skill_id!=='' && data.skill_id!==undefined){
      let tmpList = res.items.filter((item) => item[data.skill_id] > 0)
      setMatrix_List(tmpList.length > 0 ? tmpList : [])
      setTimeout(() => {
        setLoading(false)
      }, 1000);
   
    }else{
      setMatrix_List(res.items)
      setTimeout(() => {
        setLoading(false)
      }, 1000);

    }
    
    // setLoading(false)

  }

  const fetchSkill = async () => {
    const [listss] = await skill_list();
    // console.log("fetchSkill-----------", listss.results)
    // setFunction(listss.results)
    const newUsers = listss.results.map(item => ({
      value: item.skill_name,
      key: item.id
    }));
    setSkillList(prevUsers => [...prevUsers, ...newUsers]);
    setSkills(listss.results);
  };

   // Memoize the skill headers to prevent re-renders
   const skillHeaders = useMemo(() => {
    return skills.map((item) => (
      <TableCell
        key={item.id} // Add a key to help React optimize rendering
        className="c-gray fw-700 text-nowrap h3"
        sx={{ color: 'black', pl: 5, textAlign: 'left' }}
      >
        {item.skill_name}
      </TableCell>
    ));
  }, [skills]); // Will only recalculate if `skills` changes


  const handleChange = (e) => {
    let tempList = matrixList
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
    // console.log("skill matrix data",name,value)
    if (value === 123) {
      setMatrix_List(tempList)
    }
    else {
      let tmpList = tempList.filter((item) => item[value] > 0)
      setMatrix_List(tmpList.length > 0 ? tmpList : [])
      // console.log("name----", name, "value-----", value, "templist", tmpList)
    }

  };

  const handleChangee = (e) => {
    let tempList = matrixList;
    const { name, value } = e.target;
  
    // Restrict input to numbers only for 'aadhar'
    if (name === "aadhar" && !/^\d*$/.test(value)) {
      return; // Prevent non-numeric values
    }
  
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
  
    setData((prev) => ({ ...prev, [name]: value }));
  
    if (value === 123) {
      setMatrix_List(tempList);
    } else {
      let tmpList = tempList.filter((item) => item[value] > 0);
      setMatrix_List(tmpList.length > 0 ? tmpList : []);
    }
  };
  
 
  const handleByAadhar = (e) => {
    const aadharValue = e.target.value;
      const findList = findByAadhar(aadharValue);
      setMatrix_List(findList || []);

  };

  const findByAadhar = (aadhar) => {
    // Use filter instead of find to return an array of matching records (if more than one match is possible)
    return matrix_List.filter(record => record.aadhar.startsWith(aadhar));
  };

  const printRef = useRef(null)
  const handlePrintqa = useReactToPrint({
    content: () => printRef.current,
    onPrintError: () => console.log("error------"),
    removeAfterPrint: true,
  });

  const printFull = useRef(null)
  const printFullPage = useReactToPrint({
    content: () => printFull.current,
    onPrintError: () => console.log("error------"),
    removeAfterPrint: true,
  });

  function handlePrintq(val) {
    setPrintIndex(val)
    setPrintState(true)
    setTimeout(() => {
      handlePrintqa()
    }, 1000);
  }
  
    const TableBodyRow= React.memo(({ row, arr, index, childFun })=> {
      // console.log("TableBodyRow row------>>>",row)
      const getPieChartColor = useMemo(() => {
        return arr.map((item) => {
          const value = row[item.id];
          return value === 0 ? color_1 : 
                 value === 1 ? color_2 : 
                 value === 2 ? color_3 : 
                 value === 3 ? color_4 : color_5;
        });
      }, [arr, row]);

      const renderPieCharts = (colorArray) => {
        return colorArray.map((color, index) => (
          <TableCell key={arr[index].id} sx={{ p: 0 }}>
            <Stack sx={{ width: 70, pl: 5 }}>
              <PieChart chartColors={color} />
            </Stack>
          </TableCell>
        ));
      };
    return (
      <>
        <Stack sx={{ p: 0.3 }}>
        </Stack>
        <TableRow
          key={row._id}
          sx={{}}
        >
          <TableCell className="c-gray fw-500 text-nowrap h3" sx={{ color: 'black' }}>
            {index + 1}
          </TableCell>
          <TableCell className="c-gray fw-500 text-nowrap h3" sx={{ color: 'black' }}>
            {row.fullname}
          </TableCell>
          <TableCell className="c-gray fw-500 text-nowrap h3" sx={{ color: 'black' }}>
            {row.aadhar}
          </TableCell>
          {/* {
            arr.map((item, index) => {
              return (<TableCell className="" sx={{ p: 0 }}>
                <Stack sx={{ width: 70, pl: 5 }}>
                  <PieChart
                    chartColors={row[item.id] === 0 ? color_1 : row[item.id] === 1 ? color_2 : row[item.id] === 2 ? color_3 : row[item.id] === 3 ? color_4 : color_5}
                    // chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
                  />
                </Stack>
              </TableCell>
              )
            })
          } */}
          {renderPieCharts(getPieChartColor)}  {/* Render memoized pie chart colors */}
          <TableCell>
            <Tooltip title={t('printSkillMatrix')}>
              <IconButton
                sx={{ height: 25, width: 25 }}
                onClick={() => childFun(row)}
              >
                <LocalPrintshopOutlinedIcon color="primary" fontSize="small" />
              </IconButton>
            </Tooltip>
          </TableCell>
        </TableRow>
      </>
    )
  })

  const handlePageChange = (e,newPage) => {
    // console.log("handlePageChange",newPage)
    setPage(newPage);
    // setIsLoading(true);
    // Simulate fetching data for the new page (e.g., API call)
    // fetchDataForPage(newPage);
  };

  return (
    <div className="mx-3 my-2" ref={printFull}>
      <div className="flex items-center justify-between">
        <Stack sx={{ flexDirection: 'row', alignItems: 'center', width: '100%', justifyContent: 'space-between' }}>
          {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
          <Typography className="fw-20-500-36">
           {t('skillMatrix')}
          </Typography>
          <Stack sx={{ flexDirection: 'row', mt: -1 }}>
            <Stack sx={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }}>
              <Stack sx={{ width: 50, }}>
                <PieChart
                  chartColors={color_1}
                  // chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
                />
              </Stack>
              <Typography className='fw-14' textAlign='center'>{t('induction')}</Typography>
            </Stack>
            <Stack sx={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }}>
              <Stack sx={{ width: 50 }}>
                <PieChart
                  chartColors={color_2}
                  // chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
                />
              </Stack>
              <Typography className='fw-14' textAlign='center'>{t('level')} 1</Typography>
            </Stack>
            <Stack sx={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }}>
              <Stack sx={{ width: 50 }}>
                <PieChart
                  chartColors={color_3}
                  // chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
                />
              </Stack>
              <Typography className='fw-14' textAlign='center'>{t('level')} 2</Typography>
            </Stack>
            <Stack sx={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }} >
              <Stack sx={{ width: 50 }}>
                <PieChart
                  chartColors={color_4}
                  // chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
                />
              </Stack>
              <Typography className='fw-14' textAlign='center'>{t('level')} 3</Typography>
            </Stack>
          </Stack>
        </Stack>
      </div>
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <Stack sx={{ py: 1, flexDirection: 'row', gap: 1 }}>
          <SchemaExtractor
            data={data}
            errors={errors}
            onChange={handleChangee}
            schema={form2}
          />
          {/* <Input placeholder={t('search_by_aadhar')} onChange={handleByAadhar} /> */}
        </Stack>
        {/* <Button onClick={()=>fetchSkillMatrix()}>hit</Button> */}
        <Stack sx={{ flexDirection: 'row', gap: 2, pr: 4, alignItems: 'center' }}>
          <LocalPrintshopOutlinedIcon color={COLORS.BLACK} onClick={() => printFullPage()} />
          {/* <FilterAltOutlinedIcon color={COLORS.BLACK} /> */}
        </Stack>
      </div>
      <TableContainer>
        <MTable
        // sx={{ minWidth: minWidth }}
        // className={`${tableClasses}`}
        // size={dense && "small"}
        >
          <TableHead
            sx={{ backgroundColor: COLORS.BG_GRAY }}
          >
            <TableRow>
              <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
                {t('sno')}
              </TableCell>
              <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
                {t('operator')}
              </TableCell>
              <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
                {t('aadhaarNumber')}
              </TableCell>
              {skillHeaders}
              {/* {skills.map((item) => {
                return (<TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black', pl: 5, textAlign: 'left' }}>
                  {item.skill_name}
                </TableCell>
                )
              })} */}
              <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
                {t('action')}
              </TableCell>
            </TableRow>
          </TableHead>
          {/* <Stack sx={{ p: 0.2 }}></Stack> */}
          <TableBody>
            { loading ? (
              <TableRow>
                <TableCell
                colSpan={12}
                className="opacity-60 w-full"
              >
                <div style={{display:'flex',justifyContent:'center'}}>
                <Spin big blue/>
                </div>
              </TableCell>
              </TableRow>
            ) :matrix_List.length > 0 ? (
              matrix_List.map((item, index) => (
                <TableBodyRow row={item} arr={skills} index={index} childFun={handlePrintq} />
              ))
            ):(
              <TableRow>
              <TableCell
                colSpan={12}
                className="text-center h-300 flex items-center opacity-60 w-full"
              >
                <img
                  src={empty}
                  alt="leaf"
                  height={100}
                  className="w-full object-contain"
                />
                <p className="text-center">No Records found!</p>
              </TableCell>
            </TableRow>
            )}
          </TableBody>
          <TableFooter>
            <TableRow>
              <TablePagination
               rowsPerPageOptions={[]}
               count={total}
               rowsPerPage={5}
              //  onPageChange={(e, newValue) => setPage(newValue)}
               page={page} // Pagination should be zero-based
              //  onPageChange={(e, newPage) => {setPage(newPage + 1)}} 
               onPageChange={handlePageChange} 
              />
            </TableRow>
          </TableFooter>
        </MTable>
      </TableContainer>
      {printState === true && (
        <div style={{ visibility: 'hidden' }}>
          <div ref={printRef}>
            <PrintPage skill={skills} row={printIndex} />
          </div>
        </div>
      )}
    </div>
  );

  

};

export default SkillMatrix;
